import React from "react";

interface ButtonProps {
  children: React.ReactNode;
  type?: "button" | "submit" | "reset";
}

const Button: React.FC<ButtonProps> = ({ children, type = "button" }) => {
  return (
    <button
      type={type}
      className="w-full px-6 py-3 text-primary-900 border border-primary-color font-bold text-sm rounded-lg transition hover:bg-primary-color hover:text-white"
    >
      {children}
    </button>
  );
};

export default Button;
