"use client";

import React, { useState, useEffect } from "react";
import AuthService from "../services/AuthService";
import Button from "@/components/UI/Button";
import Input from "@/components/UI/Input";
import LoginError from "./LoginError";
import Link from "next/link";
import { useTheme } from "next-themes";

const LoginForm: React.FC = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState<string | null>(null);
  const [showPassword, setShowPassword] = useState(false); // Estado para mostrar/ocultar contraseña
  const { theme } = useTheme(); // Obtener el tema actual (dark o light)

  const [iconPaths, setIconPaths] = useState({
    email: "/assets/light/email-icon.svg",
    lock: "/assets/light/lock-icon.svg",
    eyeOpen: "/assets/light/eye-open.svg", // Ícono de ojo abierto
    eyeClosed: "/assets/light/eye-closed.svg", // Ícono de ojo cerrado
  });

  useEffect(() => {
    if (theme === "dark") {
      setIconPaths({
        email: "/assets/dark/email-icon.svg",
        lock: "/assets/dark/lock-icon.svg",
        eyeOpen: "/assets/dark/eye-open.svg",
        eyeClosed: "/assets/dark/eye-closed.svg",
      });
    } else {
      setIconPaths({
        email: "/assets/light/email-icon.svg",
        lock: "/assets/light/lock-icon.svg",
        eyeOpen: "/assets/light/eye-open.svg",
        eyeClosed: "/assets/light/eye-closed.svg",
      });
    }
  }, [theme]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    try {
      await AuthService.login(username, password);
      alert("Inicio de sesión exitoso");
    } catch (err: any) {
      setError(err.message);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="login-form">
      <h1 className="login-title">¡Bienvenido de nuevo!</h1>
      <h2 className="login-subtitle">Welcome Back!</h2>

      <div className="login-input-group">
        <Input
          id="username"
          type="text"
          placeholder="Ingresa tu correo electrónico"
          icon={iconPaths.email} // Cambia según el tema
          value={username}
          onChange={(e) => setUsername(e.target.value)}
        />
      </div>

      <div className="login-input-group relative">
        {/* Input de contraseña */}
        <input
          id="password"
          type={showPassword ? "text" : "password"} // Cambia el tipo dinámicamente
          placeholder="Ingresa tu contraseña"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:ring-2 focus:ring-indigo-500 focus:outline-none pr-10"
        />

        {/* Ícono del ojito */}
        <div
          onClick={() => setShowPassword(!showPassword)} // Cambiar visibilidad de la contraseña
          className="absolute right-3 top-1/2 transform -translate-y-1/2 cursor-pointer text-gray-500 hover:text-gray-700"
        >
          <img
            src={showPassword ? iconPaths.eyeOpen : iconPaths.eyeClosed} // Cambia entre ojo abierto/cerrado
            alt="Toggle password visibility"
            className="w-5 h-5"
          />
        </div>
      </div>

      <div className="login-checkbox-container">
        <label className="flex items-center">
          <input type="checkbox" className="login-checkbox" />
          Recordar por 30 días
        </label>
        <Link href="/login/pages/ForgotPasswordPage" className="login-forgot-password">
          ¿Olvidaste tu contraseña?
        </Link>
      </div>

      {error && <LoginError message={error} />}
      <Button type="submit">Iniciar sesión</Button>
    </form>
  );
};

export default LoginForm;
